<template>
	<div id="login-wrapper" data-commont='登陆页面'>
		<div class="login-bg"></div>
		<div class="login-overlay"></div>
		<div class="login-box">
			<div class="login-info">
				<h1 class="header-title"></h1>
				<p class="text-muted"></p>
				<ul>
					<li></li>
					<li></li>
					<li></li>
				</ul>
			</div>
			<div class="login-content">
				<div class="panel panel-color panel-primary">
					 <!--表单头部--> 
					<div class="panel-heading">
						<h3 class="panel-title">中国移动终端服务</h3>
					</div>
					<!-- 表单主体 -->
					<div class="panel-body">
						<div class="tab-content">
							<el-form :model="formLogin" :rules="loginRules" ref="formLogin" class="tab-form">
								<el-form-item prop="name">
									<el-input v-model="formLogin.name" placeholder="账号"></el-input>
								</el-form-item>
								<el-form-item prop="password">
									<el-input v-model="formLogin.password" placeholder="密码" type="password"></el-input>
								</el-form-item>
								 <!--<el-form-item >
									<el-input v-model="formLogin.type" placeholder="输入验证码"></el-input>
								</el-form-item> -->
								<el-form-item >
									<el-button type="primary" @click="onSubmitLogin('formLogin')" style="width:100%">立即登录</el-button>
								</el-form-item>
							</el-form>
							<div class="hr-border"></div>
							<el-button type="text">忘记密码？点击找回</el-button>
						</div>
					</div>
				</div>
			</div>

		</div>
	</div>
</template>

<script>
import {
	loginByPhone
	} from 'api/login'
import {
	getToken,
	getUserInfo,
	removeToken
} from 'utils/auth';
// 引入公共常量
import publicConst from 'utils/public_const'
export default {
  	name: 'login',
  	components: {
  		
  	},
  	data() {
	    return {
	    	formLogin:{
				name:'',
				password:'',
			},
			loginRules:{
				name:{ required: true,  message: '请输入账号', trigger: 'blur' },
				password:{required: true,  message: '请输入密码', trigger: 'blur'}
			},
	    }
  	},
  	computed: {
		
   	},
   	created(){
   		removeToken()	
   	},
   	mounted: function () {
   		console.log('mouted'+getToken())
        this.$nextTick(function () {
//      	
        })
    },
  	methods:{
		/**
		 * 登录
		 */
       	onSubmitLogin(formName){
       		this.$refs[formName].validate((valid) => {
	            if (valid) {
	            	let req = {
						txtuid:this.formLogin.name,
						txtpwd:this.formLogin.password
					}

					this.$store.dispatch('LoginByPhone',req).then((data) =>{
						console.log('首页：'+JSON.stringify(data))
						if(data.TYPE === 'S'){
							this.$router.push({
				              	name: 'echarts_list'
				            });

						}else{
							this.$message.error(data.MESSAGE);
							
							
						}
			          		
					})
	            } else {
	            	console.log('error submit!!');
	            return false;
	            }
	        });
		
		},
		
		
		
  	}
}
</script>

<style lang="scss" slot-scope="scope"> 
	$color_fff:#fff;
#login-wrapper {
    width: 100%;
    height: 100%;
    background: $color_fff;
    position: absolute;
    .login-bg {
        position: absolute;
        z-index: 1;
        width: 100%;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
        background: url(../../assets/login/login-bg.jpg) no-repeat 50%;
    }
    .login-overlay {
        width: 100%;
        height: 600px;
        background: rgba(41, 118, 234, .75);
        position: absolute;
        z-index: 2;
        top: 50%;
        transform: translateY(-50%);
        left: 0;
        right: 0;
    }
    .login-box {
        width: 990px !important;
        margin: 0 auto;
        position: relative;
        z-index: 3;
        height: 100%;
        .login-info {
            position: absolute;
            z-index: 4;
            top: 50%;
            transform: translateY(-50%);
            left: 0;
            color: $color_fff;
            .header-title {
                font-size: 46px;
                line-height: 1;
                letter-spacing: 4px;
                font-weight: 400;
                font-family: Microsoft YaHei;
            }
            .text-muted {
                font-size: 18px;
                letter-spacing: 4px;
            }
            ul {
                list-style: none;
                padding: 0;
                margin: 80px 0 0;
                li {
                    position: relative;
                    font-size: 16px;
                    letter-spacing: 2px;
                    padding: 10px 0 10px 34px;
                    &:before {
                        display: block;
                        content: "";
                        box-sizing: content-box;
                        position: absolute;
                        top: 20px;
                        left: 5px;
                        width: 6px;
                        height: 6px;
                        background: $color_fff;
                        border-radius: 100%;
                        box-shadow: 0 0 0 4px hsla(0, 0%, 100%, .3);
                    }
                }
            }
        }
        .login-content {
            width: 370px;
            height: 480px;
            background-color: $color_fff;
            position: absolute;
            z-index: 6;
            top: 50%;
            transform: translateY(-50%);
            right: 0;
            border-radius: 4px 4px 3px 3px;
            box-shadow: 0 0 6px rgba(60, 122, 208, .45);
            .panel {
                margin-bottom: 0;
                border-radius: 0;
                box-shadow: none;
                background: transparent;
                .panel-body {
                    padding: 0 35px;
                    .tab-content {
                        color: #777;
                        .tab-pane {
                            display: none;
                        }
                        .active {
                            display: block;
                        }
                        .tab-form {
                            padding-top: 30px !important;
                        }
                    }
                }
            }
            .panel-primary {
                color: $color_fff;
                .panel-heading {
                    height: 55px;
                    padding: 0;
                    padding-left: 20px;
                    background-color: #6da7ff;
                    border: none;
                    border-top-left-radius: 3px;
                    border-top-right-radius: 3px;
                    .panel-title {
                        margin: 0;
                        text-transform: uppercase;
                        letter-spacing: .03em;
                        line-height: 55px;
                        font-size: 18px;
                        font-weight: 400;
                        font-family: Microsoft YaHei;
                    }
                }
            }
        }
    }
}
</style>



